<template>
  <el-row>
    <el-col :span="18">
      <el-card style="
          height: 90vh !important;
          max-height: 90vh !important;
          overflow: scroll;
        ">
        <h4 class="pt-3 ps-3">My Applications</h4>
        <el-divider />
        <div v-for="job in applications" :key="job">
          <div class="ps-3 pe-3">
            <h6 class="text-info">{{ job.job_title }}</h6>
            <p>
              {{ job.job_type }} | {{ job.career_level }} |
              {{ job.experience }} | {{ job.industry }} |
              {{ job.location + ", " + job.country }} |
              {{ $helpers.dateFormat(job.application_deadline) }}
            </p>
            <h6>Description</h6>
            <div class="job-detail-outer" v-html="job.description"></div>
            <h6>Skills</h6>
            <div class="job-detail-outer" v-html="job.skills"></div>
            <el-button type="warning" :disabled="job.applied" plain
              @click="(application.job_id = job.id), (dialog = true)">{{
                job.applied ? "Applied" : "Apply Now"
              }}</el-button>
          </div>
          <el-divider />
        </div>
      </el-card>
    </el-col>
    <el-col :span="6">
      <el-card class="p-3" style="
          height: 90vh !important;
          max-height: 90vh !important;
          overflow: scroll;
        ">
        <h6>Complete Your Profile</h6>

        <el-timeline class="mt-5">
          <el-timeline-item type='primary'><router-link to="/career/profile"> Personal Information</router-link>
          </el-timeline-item>
          <el-timeline-item type='primary'><router-link to="/career/profile"> Education</router-link>
          </el-timeline-item>
          <el-timeline-item type='primary'><router-link to="/career/profile"> Work Experience</router-link>
          </el-timeline-item>
          <el-timeline-item><router-link to="/career/profile"> Skills</router-link>
          </el-timeline-item>
        </el-timeline>
      </el-card>
    </el-col>
  </el-row>
</template>
<style scoped>
.el-card {
  margin: 0.2rem;
}

.card-body {
  padding: 0.6rem;
}

.text-muted {
  font-size: 14px;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

a {
  color: black;
}
</style>
<script>
export default {
  data() {
    return {
      loading: false,
      applications: [],
      application: {},
    };
  },
  mounted() {
    this.myApplications();
  },
  methods: {
    myApplications() {
      this.$endpoint.myApplications().then(
        (res) => {
          this.loading = false;
          this.applications = res.data.data;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
  },
};
</script>